import React, { Component } from 'react';
import TextInput from './TextInput.js';
import * as Rates from '../lib/Rates.js';
import Select from 'react-select';
import 'react-select/dist/react-select.css';
import avList from './../data/av-all.json';
import Fuse from 'fuse.js';
import { connect } from 'react-redux'
import { setFormFields } from '../actions'

class InputForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            rateClass: "2000",
            av: 5000,
            vehicleValue: 0,
            USD2MMK: 1360,
            JPY2MMK: 12,
            slipValue: 11000000,
            carOrigin: 'carFromJPY',
            avSeach: "Search by Model & Year...",
        };

        this.rateClasses = [
            {class: Rates.Rate0, name:"0", label:"1350cc အောက်"},
            {class: Rates.Rate1350, name:"1350", label:"1350cc-1500cc"},
            {class: Rates.Rate1500, name:"1500", label:"1500cc-2000cc"},
            {class: Rates.Rate2000, name:"2000", label:"2000cc-4000cc"},
            {class: Rates.Rate4000, name:"4000", label:"4000cc-5000cc"},
            {class: Rates.Rate5000, name:"5000", label:"5000cc အထက်"},
            {class: Rates.RatePickUp, name:"PickUp", label:"ပစ်ကပ်"},
            {class: Rates.RateElectric, name:"Electric", label:"လျှပ်စစ်ကား"},
        ];

        this.avArray = Object.keys(avList).map(function(key) {
            return { label: key, value: avList[key]};
        }) ;
    }

    getOptions(input, callback) {

        let result = [];

        if (input) {

            const options = {
                shouldSort: true,
                tokenize: true,
                matchAllTokens: true,
                findAllMatches: true,
                threshold: 0.3,
                location: 0,
                distance: 200,
                maxPatternLength: 32,
                minMatchCharLength: 2,
                keys: [
                    "label"
                ]
            };

            let fuse = new Fuse(this.avArray, options); // "list" is the item array
            result = fuse.search(input.trim());

            result = result.slice(0, 10);
        }

        callback(null, {options: result, complete: true});
    }

    carFromJPY() {
        return this.state.carOrigin === 'carFromJPY';
    }

    getRate() {
        const selectedRateClass = this.state.rateClass;

        let rate = 0;
        for (let c of this.rateClasses) {
            if (c.name === selectedRateClass) {
                rate = new (c.class)();
            }
        }

        return rate;
    }

    reportFieldsUpdate() {
        const { av, USD2MMK, JPY2MMK, slipValue } = this.state;

        const carFromJPY = this.carFromJPY();

        const vehicleValue = this.state.vehicleValue || 0;
        const vehicleValueInMMK = carFromJPY ? vehicleValue * JPY2MMK : vehicleValue * USD2MMK;

        const newFields = { av, rate: this.getRate(), USD2MMK, vehicleValueInMMK, slipValue };

        const { onFormFieldDidUpdate } = this.props;

        onFormFieldDidUpdate(newFields);
    }

    componentDidMount() {
        this.reportFieldsUpdate();
    }

    componentDidUpdate() {
        this.reportFieldsUpdate();
    }

    render() {
        const selectedRateClass = this.state.rateClass;

        const rate = this.getRate();

        const carFromJPY = this.carFromJPY();

        return (
            <div>
                <div className="col-md-5">
                    <form className="form">
                        <div className="form-group">
                            <h4>၁။ Engine Power ရွေးပါ</h4>
                            <div onChange={e => this.setState({ rateClass: e.target.value })}>
                                {this.rateClasses.map((rc)=>{
                                    return (<div className="radio col-md-12"><label><input type="radio" value={rc.name} id={rc.name} checked={selectedRateClass === rc.name} />{rc.label}</label></div>)
                                })}
                            </div>
                        </div>
                        <a className="btn btn-primary" data-toggle="collapse" href="#taxRates" aria-expanded="false" aria-controls="taxRates">
                            နှုံးထားများ
                        </a>
                        <div id="taxRates" className="collapse" style={{fontSize: '.85em'}}>
                            <TextInput id="customDuty" suffix="%" type="text" label="အကောက်ခွန်" value={rate.customDuty}/>
                            <TextInput id="specialGoodsTax" suffix="%" type="text" label="အထူးကုန်စည်ခွန်" value={rate.specialGoodsTax}/>
                            <TextInput id="commercialTax" suffix="%" type="text" label="ကုန်သွယ်ခွန်" value={rate.commercialTax}/>
                            <TextInput id="incomeTax" suffix="%" type="text" label="ဝင်ငွေခွန်" value={rate.incomeTax}/>
                            <TextInput id="kaNyaNaTax" suffix="%" type="text" label="ကညန" value={rate.kaNyaNaTax}/>

                            <hr/>
                        </div>
                        <div>
                            <h4>၂။ အောက်ပါတန်ဖိုးများကိုဖြည့်ပါ</h4>

                            <label>စည်းကြပ်တန်ဖိုး AV/CIF</label>

                            <div className="row">
                                <div className="col-md-6">
                                    <Select.Async
                                        name="av-search"
                                        autoload={false}
                                        cache={false}
                                        loadOptions={this.getOptions.bind(this)}
                                        placeholder={this.state.avSeach}
                                        onChange={sel => this.setState({avSeach: sel.label, av:sel.value})}
                                        filterOptions={options => options}
                                    />
                                </div>
                                <div className="col-md-6">
                                    <TextInput id="av" prefix="$" label="" type="text" value={this.state.av} onChange={v => this.setState({ av: v })} />
                                </div>
                            </div>
                            <label>ကားတန်ဖိုးငွေကြေး</label>
                            <div onChange={e => this.setState({ carOrigin: e.target.value })}>
                                <div className="radio col-md-12">
                                    <label>
                                        <input type="radio" value="carFromJPY" checked={carFromJPY} />JPY
                                    </label>
                                </div>
                                <div className="radio col-md-12">
                                    <label>
                                        <input type="radio" value="carFromUSD" checked={!carFromJPY} />USD
                                    </label>
                                </div>
                            </div>
                            <TextInput id="vehicleValue" prefix={carFromJPY ? "¥" : "$"} type="text" label="ကားတန်ဖိုး" value={this.state.vehicleValue} onChange={v => this.setState({ vehicleValue: v })} />
                            <TextInput id="slipValue" prefix="ကျပ်" type="text" label="အပ်ကား (စလစ်)" value={this.state.slipValue} onChange={v => this.setState({ slipValue: v })} />
                            <div className="row">
                                <div className="col-md-6">
                                    <TextInput id="USD2MMK" type="text" label="USD to MMK" value={this.state.USD2MMK} onChange={v => this.setState({ USD2MMK: v })} />
                                </div>
                                <div className={carFromJPY ? "col-md-6" : "hidden"}>
                                    <TextInput id="JPY2MMK" type="text" label="JPY to MMK" value={this.state.JPY2MMK} onChange={v => this.setState({ JPY2MMK: v })} />
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onFormFieldDidUpdate: fields => {
            dispatch(setFormFields(fields));
        }
    }
};

InputForm = connect(null, mapDispatchToProps)(InputForm);

export default InputForm;


