class TaxCalculator {

  constructor(av, rate, USD2MMK, vehicleValueInMMK, slipValueInMMK) {
    this.av = av - 0;
    this.rate = rate;
    this.USD2MMK = USD2MMK;
    this.vehicleValueInMMK = vehicleValueInMMK;
    this.slipValueInMMK = slipValueInMMK;
  }

  customDuty() {
    return this.av * (this.rate.customDuty/100);
  }

  specialGoodsTax() {
    return (this.av + this.customDuty()) * (this.rate.specialGoodsTax/100);
  }

  commercialTax() {
    return (this.av + this.customDuty() + this.specialGoodsTax()) * (this.rate.commercialTax/100);
  }

  incomeTax() {
    return (this.customDuty() + this.specialGoodsTax() + this.commercialTax()) * (this.rate.incomeTax/100);
  }

  kaNyaNaTax() {
    return (this.av * 0.8333) * (this.rate.kaNyaNaTax/100);
  }

  totalTax() {
    return this.customDuty() + this.specialGoodsTax() + this.commercialTax() + this.incomeTax() + this.kaNyaNaTax();
  }

  totalTaxInMMK() {
    return this.totalTax() * this.USD2MMK;
  }

  preIncomeTax() {
    const TTinMMK = this.totalTaxInMMK();
    if (TTinMMK < 30000000) {
      return TTinMMK * 0.15;
    } else if (TTinMMK < 100000000) {
      return ((TTinMMK - 30000000) * 0.2) + 4500000;
    } else if (TTinMMK > 100000000) {
      return ((TTinMMK - 100000000) * 0.3) + 18500000;
    }
  }

  taxAfterPreIncome() {
    return this.totalTaxInMMK() + this.preIncomeTax();
  }

  grandTotal() {
    return this.taxAfterPreIncome() + this.vehicleValueInMMK + this.slipValueInMMK;
  }
}

export default TaxCalculator;


