import React, { Component } from 'react';
import * as Utils from './../utils.js';

class TextInput extends Component {
	render() {
		const {id, type, label, value, placeholder, prefix, suffix, onChange} = this.props;
		const isInputGroup = prefix || suffix;
		
		let whenChange = (e) => {
			const formatted = e.target.value;
			const value = formatted.replace(/[^0-9.]/g,'') - 0;
			if (onChange) onChange(value);
		};

		return (
			<div>
				{isInputGroup && label ? <label htmlFor={id}>{label}</label> : ""}
				<div className={isInputGroup ? "input-group" : "form-group"}>
					{!isInputGroup && label ? <label htmlFor={id}>{label}</label> : ""}
					{prefix ? <span className="input-group-addon">{prefix}</span> : ""}
						<input
						type={type}
						className="form-control input-sm"
						id={id}
						value={Utils.formatNumber(value)}
						placeholder={placeholder}
						onChange={whenChange}
						ref={el => this.el = el} />
					{suffix ? <span className="input-group-addon">{suffix}</span> : ""}
				</div>
			</div>
		);
	}
}

export default TextInput


